  padding: 20px;
  font-size: 1.4rem;
  line-height: 32px;
}
.timeline .timelineStep.evenStep .timelineStepContent {
  order: 2;
}
.timeline .timelineStep:after {
  content: "";
  border-radius: 500px;
  border: 3px solid #015d72;
  background-color: #f1efe0;
  position: absolute;
  height: 21px;
  width: 21px;
  left: calc(50% - 15px);
